import "./HomeImageCard.scss";

import React from "react";
import PropTypes from "prop-types";

import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

import HomeRole from "../../../assets/home-role.gif";
import HomeWork from "../../../assets/home-work.gif";
import HomeFinance from "../../../assets/home-finance.gif";
import HomeSocial from "../../../assets/home-social.gif";

import HomeWorking from "../../../assets/home-working.png";
import HomeTaskSystem from "../../../assets/home-task-system.png";
import HomeTrade from "../../../assets/home-trade.png";
import HomeTax from "../../../assets/home-tax.png";

function getImage(alt) {
  switch (alt) {
    case "role":
      return HomeRole;
    case "work":
      return HomeWork;
    case "finance":
      return HomeFinance;
    case "social":
      return HomeSocial;
    case "working":
      return HomeWorking;
    case "task system":
      return HomeTaskSystem;
    case "trade":
      return HomeTrade;
    case "tax":
      return HomeTax;
    default:
      return "";
  }
}

function HomeImageCard(props) {
  const {
    children, xs, sm, md, lg, xl, className, variant, alt,
  } = props;

  let image = <div className="HomeImageCard__FakeImage--1" />;
  if (alt !== "") {
    image = <img src={getImage(alt)} alt={alt} className="w-100" />;
  }

  if (variant === 0) {
    return (
      <Col
        xs={xs}
        sm={sm}
        md={md}
        lg={lg}
        xl={xl}
        className={`${className}`}
      >
        <div className="HomeImageCard__Frame--1 d-flex justify-content-center p-3 mb-4">
          {image}
        </div>
        {children}
      </Col>
    );
  }

  return (
    <Col
      xs={xs}
      sm={sm}
      md={md}
      lg={lg}
      xl={xl}
      className={`${className} HomeImageCard__Frame--2 p-4`}
    >
      <Row className="px-3">
        {image}
      </Row>
      <Row className="px-3">
        <Col className="HomeImageCard__Text--2 py-3">
          {children}
        </Col>
      </Row>
    </Col>
  );
}

HomeImageCard.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.element,
    PropTypes.arrayOf(PropTypes.oneOfType([
      PropTypes.element,
      PropTypes.string,
    ])),
  ]),
  xs: PropTypes.string,
  sm: PropTypes.string,
  md: PropTypes.string,
  lg: PropTypes.string,
  xl: PropTypes.string,
  className: PropTypes.string,
  variant: PropTypes.number,
  alt: PropTypes.string,
};

HomeImageCard.defaultProps = {
  children: "",
  xs: "",
  sm: "",
  md: "",
  lg: "",
  xl: "",
  className: "",
  variant: 0,
  alt: "",
};

export default HomeImageCard;
