const ROLE_SYSTEM_TAB_TITLE = ["Role", "Work", "Finance", "Social"];

function getRoleSystemTabButton(count, current, setter) {
  return (
    <Col
      xs="6"
      md="3"
      lg="12"
      className={`fs-5 py-2 ps-4 ${count !== 0 && "mt-0 mt-lg-2"} ${current === count && "Home__RoleSystem--selected"}`}
    >
      <button
        type="button"
        className="button-invisible text-white fw-bold"
        onClick={() => setter(count)}
      >
        {ROLE_SYSTEM_TAB_TITLE[count]}
      </button>
    </Col>
  );
}

function getRoleSystemTab(count, variant) {
  const { alt, content } = ROLE_SYSTEM_TAB[count];

  if (variant === 0) {
    return (
      <HomeImageCard
        className={`text-secondary ${count !== 0 && "mt-4"}`}
        variant={variant}
        alt={alt}
      >
        <div className="m-3">
          <h2 className="fw-bold">{ROLE_SYSTEM_TAB_TITLE[count]}</h2>
          <p>{content}</p>
        </div>
      </HomeImageCard>
    );
  }

  return (
    <HomeImageCard className="text-secondary" variant={variant} alt={alt}>
      {content}
    </HomeImageCard>
  );
}

function getCreateRoleDialog(router, index) {
  return (
    <Row className="Home__RoleDialog justify-content-center p-4">
      <Col xs="auto">
        <Row>
          <h1 className="fw-bold text-white text-center p-1 m-0 fs-2">
            {index === 0 ? "Role1" : "New role"}
          </h1>
        </Row>
        <Row className="justify-content-center mt-2">
          <Col xs="auto" className="p-0">
            <WoodenButton
              color="black"
              onClick={index === 0 ? router("/role/manage") : router("/role")}
            >
              {index === 0 ? "Manage" : "Create"}
            </WoodenButton>
          </Col>
        </Row>
      </Col>
    </Row>
  );
}

function getRoleTab(index, router) {
  return (
    <Col
      xs="12"
      md="4"
      className={`HomeBlock__Role--${index + 1} d-flex py-3`}
    >
      {index === 0 && <div style={{ height: "40vh" }} />}
      {getCreateRoleDialog(router, index)}
      <div className="d-none d-lg-block" style={{ height: "7rem" }} />
    </Col>
  );
}

